.feed-container {
    margin: 0 auto;
    border-top: 1px solid rgba(0, 0, 0, .2);
    // border: 1px solid rgba(0, 0, 0, .2);
    /* --- account --- */
    .feed-account {
        height: 9%;
        display: flex;
        align-items: center;
        .account-img {
            border-radius: 50%;
            background-color: rgba(0, 0, 0, .12);
        }
        .account-name {
            font-weight: 600;
            color: rgba(0, 0, 0, .8);
        }
    }
    /* --- thumnail --- */
    .feed-thumnail {
        width: 100%;
        height: 60%;
        background-color: rgba(0, 0, 0, .15);
    }
    /* --- buttons --- */
    .feed-btns {
        position: relative;
        // border: 1px solid red;
        i {
            text-align: center;
            color: rgba(0, 0, 0, .8);
            // border: 1px solid red;
            &:last-child {
                position: absolute;
                right: 0;
                margin-right: 0;
            }
            &.heart {
                color: red;
            }
        }
    }
    /* --- details --- */
    .feed-details {
        // border: 1px solid red;
        .details-title {
            font-weight: 600;
        }
    }
}


// Desktop
@include desktop($desktop-size) {
    .feed-container {
        width: $desktop-size * 36;
        height: $desktop-size * 40;
        /* --- account --- */
        .feed-account {
            margin-left: $desktop-size;
            .account-img {
                width: $desktop-size * 2.2;
                height: $desktop-size * 2.2;
            }
            .account-name {
                margin-left: $desktop-size;
            }
        }
        /* --- buttons --- */
        .feed-btns {
            margin: $desktop-size $desktop-size $desktop-size*1.2;
            i {
                font-size: $desktop-size * 1.6;
                margin-right: $desktop-size * 0.5;
                // border: 1px solid red;
            }
        }
        /* --- details --- */
        .feed-details {
            margin: 0 $desktop-size;
            .details-title {
                margin-bottom: $desktop-size;
            }
        }
    }
}

// Tablet
@include tablet($tablet-size) {
    .feedbox {
        margin: $tablet-size auto;
        width: $tablet-size * 16;
        height: $tablet-size * 16;
    }
}

// Mobile
@include mobile($mobile-size) {
    .feedbox {
        margin: $mobile-size auto;
        width: $mobile-size * 13;
        height: $mobile-size * 13;
    }
}